<template>
  <div id="edm-template-card-component" @click="onCardClick">
    <div class="wrapper">
      <div class="top"></div>
      <div class="middle"></div>
      <div class="bottom">
        <div class="first"></div>
        <div></div>
      </div>
    </div>
    <div v-if="editable" class="edit-icon">
      <font-awesome-icon
        class="icon"
        :icon="['fas', 'edit']"
      ></font-awesome-icon>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";

import { EdmCardResponse } from "@/typings/edm";

const Component = defineComponent({
  name: "EDMTemplateCard",
  props: {
    data: {
      type: Object as PropType<EdmCardResponse>,
      default: () => ({})
    },
    editable: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const router = useRouter();
    const { data } = toRefs(props);

    const onCardClick = async () => {
      await router.push({ name: "EDMDetail", params: { id: data.value.id } });
    };

    return {
      onCardClick
    };
  }
});

export default Component;
</script>

